.card {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.card {

    box-sizing: border-box;
    border-radius: 2px;
    background-clip: padding-box;
    background: #fff;
    padding: 10px 10px;
    margin-bottom: 10px;
}

.panel-heading {
    font-family: ff2;
}

table td {
    color: #000AD2 !important;
}