.form-control {
    font-size: 14px;
    /* line-height: 1.6; */
    color: var(--form-text-color);
    display: block;
    border-color: var(--form-border-color);
    position: relative;
}

label {
    font-weight: 700;
}

.control-label {
    text-align: right;
    margin-bottom: 0;
    padding-top: 7px;
}

.checkbox label,
.radio label {
    min-height: 22px;
    padding-left: 8px;
    margin-bottom: 0;
    font-weight: 400;
    cursor: pointer;
}

.form-group {
    margin-bottom: 15px;
}

.form-control::placeholder {
    color: #9b9b9b;
}