@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,600');

@font-face {
    font-family: ff1;
    src: url('../fonts/Dosis-Regular.otf');
}

@font-face {
    font-family: ff2;
    src: url('../fonts/bold.ttf');
}

@font-face {
    font-family: ff3;
    src: url('../fonts/digital.ttf');
}

/* Common Css */
@import url('../CSS/Common/formstyles.css');
@import url('../CSS/Common/buttons.css');
@import url('../CSS/Common/misc.css');
@import url('../CSS/Common/sidenav.css');
@import url('../CSS/Common/font.css');


/* Pages CSS */
@import url('../CSS/Pages/login.css');
@import url('../CSS/Pages/dashboard.css');
@import url('../CSS/Pages/credittransfer.css');
@import url('../CSS/Pages/transactionReport.css');


/* Responsive CSS */
@import url('./Common/responsive.css');

:root {
    --text: #636b6f;
    --form-text-color: #555;
    --form-border-color: #ccd0d2;
    --link-color: #3097D1;
    --panel-border-color: #d3e0e9;
}

body {
    font-family: ff1;
    font-size: 14px;
    line-height: 1.6;
    color: var(--text);
}