.navbar {
    position: relative;
    min-height: 50px;
    margin-bottom: 22px;
    border: 1px solid transparent;
}

.navbar-default .navbar-brand {
    color: #777;
}

.navbar-brand {
    font-size: 18px;
}

.navbar-default .navbar-nav>li>a,
.navbar-default .navbar-text {
    color: #777;
}

.navi ul li a {
    color: #fff !important;
}

.nav>li>a {
    position: relative;
    display: block;
    padding: 5px 15px;
}

.navbar-header .navbar-toggler {
    border-color: #ddd;
}

.navi .navbar-brand {
    color: #fff;
}

.navbar-toggler {
    position: relative;
    float: right !important;
    padding: 9px 10px;
    margin-top: 8px;
    margin-right: 15px;
    margin-bottom: 8px;
    background-color: transparent;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
}

.navi {
    background: #e57373;
    background: #BFB52D;
    border-bottom: 5px solid #ef9a9a;
    border-bottom: 5px solid #3A01B1;
}

.visible-xs {
    display: none;
}

.blox {
    font-size: 13px;
    font-family: ff2;
}

.blox span,
.breadcrumb.blox span {
    font-weight: 500;
    font-family: ff1;
    font-size: 20px;
}

.breadcrumb.blox span {
    display: block;

}

.breadcrumb {
    padding: 8px 15px;
    margin-bottom: 20px;
    list-style: none;
    background-color: #f5f5f5;
    border-radius: 4px;
}