.btn {
    font-size: 14px;
}

.btn-primary {
    color: #fff;
    background-color: var(--link-color);
    border-color: #2a88bd;
}

.btn-link {
    color: var(--link-color);
    font-weight: 400;
    border-radius: 0;
}

a {
    color: var(--link-color);
    text-decoration: none !important;
}

a {
    font-size: 15px;
}